
h1,
.h1 {
  font-family: 'rennerlight';
  font-size: 56px;
  color: #2D2D2D;
}
h2,
.h2 {
  font-family: 'rennerbold';
  font-size: 24px;
  color: #2D2D2D;
}
h3,
.h3 {
  font-family: 'rennerlight';
  font-size: 36px;
  color: #2D2D2D;
}
p,
.p,
ul,
li,
ol,
dl,
dt,
dd {
  font-family: 'rennerbook';
  font-size: 24px;
  color: #666666;
}
a,
strong,
i,
b {
  font-family: inherit;
}
.small {
  font-size: 85%;
}
.button {
  font-family: 'rennerbook';
  font-size: 20px;
}
.emph {
  font-family: 'rennerbook';
  font-size: 48px;
  line-height: 35px;
}
a {
  color: #792822;
}
a:hover, a:focus {
    color: #9e0b0f;
}



